<template>
  <div class="my-music">
    <div class="album-slide">
      <div class="album-img">
        <img :src="attachImageUrl(avatar)">
      </div>
      <ul class="album-info">
        <li>: {{username}}</li>
        <li>: {{name}}</li>
        <li>: {{userSex}}</li>
        <li>: {{birth}}</li>
        <li>: {{location}}</li>
      </ul>
    </div>
    <div class="album-content">
      <div class="album-title">
        : {{introduction}}
      </div>
      <div class="songs-body">
        <album-content :song-list="collectList">
          <template slot="title"></template>
        </album-content>
      </div>
    </div>
  </div>
</template>

<script>
import {mixin} from '../mixins';
import {mapGetters} from "vuex";
import {getUserOfId , getCollectOfUserId , songOfSongId} from "../api";
import AlbumContent from "../components/AlbumContent.vue";
export default {
  name: 'myMusic',
  mixins : [mixin],
  components:{
    AlbumContent
  },
  data() {
    return {
      avatar : '',    // 
      username : '',     // 
      name : '',        // 
      userSex : '' ,      // 
      birth: '',          //  
      location: '',       //  
      introduction: '' ,   //  
      collection: [] ,     // 
      collectList : [],     // 
    }
  },
  computed:{
    ...mapGetters([
      'listOfSongs',    // 
      'userId'          // id
    ])
  },
  mounted() {
    this.getMsg(this.userId);
    this.getCollection(this.userId);
  },
  methods:{
    getMsg(userId){
      // console.log('userid:'+this.userId);
      getUserOfId(userId)
        .then(res => {
          this.avatar = res.profilePicture;
          this.username = res.username;
          this.name = res.name;
          if(res.sex === 0){
            this.userSex = '';
          }
          else if(res.sex === 1){
            this.userSex = '';
          }
          this.birth = this.getBirth(res.birth);
          this.location = res.location;
          this.introduction = res.introduction;

        })
        .catch(err => {
          console.log(err);
        })
    },
    // 
    getCollection(userId){
      getCollectOfUserId(userId)
        .then(res => {
          this.collection = res;
          // id
          for(let item of this.collection){
            if(item.songId !== null){
              this.getSongsOfId(item.songId);
            }
          }
        })
        .catch(err => {
          console.log(err);
        })
    },
    // id
    getSongsOfId(id){
      songOfSongId(id)
        .then(res => {
          this.collectList.push(res);
        })
        .catch(err => {
          console.log(err);
        })
    }

  }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/my-music.scss';
</style>



