import {getCollectOfUserId, likeSongOfName} from "../api";
import {mapGetters} from "vuex";

export const mixin = {
  computed: {
    ...mapGetters([
      'loginIn',    // 
      'userId'      // id
    ])
  },
  methods: {
    // 
    notify(title,type){
      this.$notify({
        title: title,
        type: type
      })
    },
    // 
    attachImageUrl (srcurl){
      if(srcurl){
        return this.$store.state.configure.HOST + srcurl;
      }
      else{
        return '../assets/img/user.jpg';
      }
    },
    // 
    getSong(){
      if(!this.$route.query.keywords){
        this.notify('','warning');
      }
      else{
        likeSongOfName(this.$route.query.keywords).then(res => {
          if((!res.length)){
            this.$store.commit('setListOfSongs',[]);
            this.notify('','warning');
          }
          else{
            this.$store.commit('setListOfSongs',res);
          }
        }).catch(err => {
          console.log(err)
        })
      }
    },
    //  -- 
    replaceLName(str){
      let arr = str.split('-');
      return arr[0];
    },
    // -- 
    replaceFName(str){
      let arr = str.split('-');
      return arr[1];
    },
    // 
    toplay: function (id,url,pic,index,name,lyric){
      this.$store.commit('setId',id);
      this.$store.commit('setUrl' ,this.$store.state.configure.HOST + url);
      this.$store.commit('setPicUrl',this.$store.state.configure.HOST + pic);
      this.$store.commit('setListIndex',index);
      this.$store.commit('setTitle',this.replaceFName(name));
      this.$store.commit('setArtist',this.replaceLName(name));
      this.$store.commit('setLyric',this.parseLyric(lyric));

      this.$store.commit('setIsActive' , false);
      if(this.loginIn){
        getCollectOfUserId(this.userId)
          .then(res => {
            // 
            for(let item of res){
              if(item.songId === id){
                this.$store.commit('setIsActive', true);
                break;
              }
            }
          })
      }
    },
    // 
    parseLyric(text){
      let lines = text.split("\n");         // 
      let pattern = /\[\d{2}:\d{2}.(\d{3}|\d{2})]/g;     // 
      let result = [];
      // 
      if(!(/\[.+]/.test(text))){
        return [[0,text]];
      }
      // 
      while(lines.length!==0 && !pattern.test(lines[0])){
        lines = lines.slice(1);
      }
      // 
      for(let item of lines){
        let time = item.match(pattern);     // 
        let value = item.replace(pattern,'')    // 
        for(let item1 of time){
          let t = item1.slice(1,-1).split(":");   // 
          if(value!==''){
            result.push([parseInt(t[0],10)*60+ parseInt(t[1]),value]);
          }
        }
      }
      //  --  -- 
      result.sort(function (a , b){
        return a[0] - b[0];
      });
      return result;
    },
    getBirth(value){
      return value.slice(0,10);
    },

  }
}




